.button {
  width: 15.5rem;
  height: 3.7rem;
  color: $color-white;
  border: 1px solid $color-white;
  margin: 0 1.5rem;
  font-weight: 700;

  &.primary {
    background: $color-primary;
    border: 1px solid $color-primary;
  }
  &.black {
    border: 1px solid $color-black;
    color: $color-black;
    margin-left: 0;
  }
  &.small {
    width: auto;
    height: auto;
    border-radius: 1rem;
    padding: 0.8rem 2rem;
    font-weight: 700;
    border: none;
    margin: 0 1.5rem 4rem;
    background-color: $color-black;
    display: inline;
  }
}