/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button, input, select, textarea {
  background-color: transparent;
  outline: none;
  border: 0;
}

a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
}

button, i {
  cursor: pointer;
}

/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
body {
  box-sizing: border-box;
  position: relative;
}

a {
  color: #fff;
}

.wrapper {
  max-width: 114rem;
  margin: 0 auto;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
}

.heading-primary {
  color: #fff;
  font-weight: 700;
  font-size: 6rem;
  margin: 4rem 0;
}

.heading-secondary {
  color: #fff;
  font-weight: 700;
  font-size: 3.6rem;
}

.heading-tertiary {
  color: #fff;
  font-weight: 700;
  font-size: 2.2rem;
}

.paragraph-bold {
  font-weight: 700;
}
.paragraph-italic {
  font-style: italic;
}
.paragraph-light {
  font-weight: 100;
}

.u-light {
  color: #333;
}

.u-margin-bottom-small {
  margin-bottom: 1.5rem !important;
}

.u-margin-bottom-medium {
  margin-bottom: 4rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-margin-bottom-medium {
    margin-bottom: 3rem !important;
  }
}

.u-margin-bottom-big {
  margin-bottom: 8rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-margin-bottom-big {
    margin-bottom: 5rem !important;
  }
}

.u-margin-top-big {
  margin-top: 8rem !important;
}

.u-margin-top-huge {
  margin-top: 10rem !important;
}

.button {
  width: 15.5rem;
  height: 3.7rem;
  color: #fff;
  border: 1px solid #fff;
  margin: 0 1.5rem;
  font-weight: 700;
}
.button.primary {
  background: #e84118;
  border: 1px solid #e84118;
}
.button.black {
  border: 1px solid #333;
  color: #333;
  margin-left: 0;
}
.button.small {
  width: auto;
  height: auto;
  border-radius: 1rem;
  padding: 0.8rem 2rem;
  font-weight: 700;
  border: none;
  margin: 0 1.5rem 4rem;
  background-color: #333;
  display: inline;
}

.progress-bar {
  width: 36rem;
  height: 2rem;
  background-color: #fff;
  position: relative;
  z-index: 1;
  margin: 3.5rem 0;
  border-radius: 1rem;
}
.progress-bar::before {
  content: "";
  position: absolute;
  top: 0;
  height: 2rem;
  background-color: #e84118;
  border-radius: 1rem;
  z-index: 10;
}
.progress-bar:nth-of-type(1)::before {
  width: 90%;
}
.progress-bar:nth-of-type(2)::before {
  width: 80%;
}
.progress-bar:nth-of-type(3)::before {
  width: 90%;
}
.progress-bar:nth-of-type(4)::before {
  width: 90%;
  margin: 0;
}

.sns {
  margin-bottom: 4rem;
}
.sns i:not(:last-child) {
  margin-right: 2.2rem;
}

.about-me {
  max-width: 114rem;
  margin: 0 auto;
  color: #333;
  display: flex;
  padding: 12rem 0;
}
.about-me__img {
  width: auto;
  max-height: 100%;
  object-fit: cover;
}
.about-me__container {
  padding-left: 3.4rem;
}
.about-me h3 {
  margin: 2.5rem 0;
}
.about-me .sns {
  margin-top: 4rem;
}
.about-me p {
  line-height: 2;
  color: #777;
}
.about-skill {
  background: linear-gradient(rgba(0, 98, 102, 0.9), rgba(0, 98, 102, 0.9)), url(../img/about-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 12rem 0;
}
.about-skill__container {
  max-width: 114rem;
  margin: 0 auto;
  display: flex;
}
.about-skill__wrapper {
  width: 36rem;
}
.about-skill__wrapper:not(:last-child) {
  margin-right: 3rem;
  margin-bottom: 0;
}
.about-skill h2 {
  margin-bottom: 4.5rem;
  position: relative;
  z-index: 1;
}
.about-skill h2::before, .about-skill h2::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
}
.about-skill h2::before {
  width: 20rem;
  height: 5px;
  background: #e84118;
  z-index: 100;
}
.about-skill h2::after {
  width: 33rem;
  height: 2px;
  background: #fff;
  z-index: 90;
}
.about-skill h3:not(:first-child) {
  margin-top: 3.5rem;
}
.about-skill p {
  color: #ddd;
  margin-top: 1.7rem;
}

.header {
  max-width: 114rem;
  margin: 0 auto;
  height: 3rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  font-weight: bold;
  color: #fefefe;
  padding-top: 3rem;
  margin: 0 3rem;
}
.header__nav-list {
  display: flex;
  margin: 0 3rem;
}
.header__nav-item {
  padding-top: 3rem;
}
.header__nav-link {
  padding: 0.5rem 1.5rem;
}

.hero {
  height: 84vh;
  background: url(../img/header-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.hero__container {
  background: rgba(0, 0, 0, 0.7);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}

.portfolio {
  width: 100%;
  background: linear-gradient(rgba(243, 156, 18, 0.7), rgba(243, 156, 18, 0.7)), url(../img/portfolio-bg.jpg);
  text-align: center;
}
.portfolio__container {
  max-width: 114rem;
  margin: 0 auto;
  padding: 12rem 0;
}
.portfolio__images {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(4, 26rem);
  grid-template-rows: repeat(3, 20rem);
  row-gap: 3rem;
  column-gap: 3rem;
}
.portfolio__image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-right: 2px solid #fff;
  border-left: 2px solid #fff;
}
.portfolio__image:nth-of-type(1) {
  grid-row: 1/span 1;
  grid-column: 1/span 1;
}
.portfolio__image:nth-of-type(2) {
  grid-row: 1/span 1;
  grid-column: 2/span 1;
}
.portfolio__image:nth-of-type(3) {
  grid-row: 2/-1;
  grid-column: 1/span 2;
}
.portfolio__image:nth-of-type(4) {
  grid-row: 1/span 2;
  grid-column: 3/-1;
}
.portfolio__image:nth-of-type(5) {
  grid-row: 3/span 1;
  grid-column: 3/span 1;
}
.portfolio__image:nth-of-type(6) {
  grid-row: 3/span 1;
  grid-column: 4/span 1;
}
.portfolio h2 {
  margin-bottom: 6rem;
}

.services {
  max-width: 114rem;
  margin: 0 auto;
  padding: 12rem 0;
  text-align: center;
}
.services__cards {
  display: grid;
  grid-template-columns: repeat(3, 35.9rem);
  grid-template-rows: repeat(2, 31.5rem);
  row-gap: 6rem;
  column-gap: 3rem;
}
.services__card {
  width: 35.9rem;
  height: 31.5rem;
  background-color: #fafafa;
  border: 1px solid #ddd;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}
.services h2 {
  margin-bottom: 6.2rem;
}
.services i {
  font-size: 2.2rem;
  display: inline-block;
  width: 6rem;
  height: 6rem;
  line-height: 6rem;
  border-radius: 50%;
  background-color: #333;
  color: #fff;
  margin: 3rem 0;
}
.services h3 {
  margin-bottom: 3rem;
}
.services p {
  color: #777;
  line-height: 2;
  padding: 0 3.4rem;
}

.testimonials {
  padding: 12rem 0;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../img/testimonials-bg.jpg);
}
.testimonials__container {
  max-width: 114rem;
  margin: 0 auto;
  text-align: center;
}
.testimonials h2 {
  margin-bottom: 2rem;
}
.testimonials i {
  color: #fff;
  display: block;
  font-size: 3.6rem;
  margin-bottom: 6rem;
}
.testimonials__image {
  border-radius: 50%;
  margin-bottom: 3.5rem;
}
.testimonials__name {
  color: #e84118;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.testimonials__p {
  color: #ddd;
  line-height: 2;
}
.testimonials__p:nth-of-type(2) {
  margin-bottom: 3.5rem;
}
.testimonials__p:last-child {
  margin-bottom: 3rem;
}